html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Rubik", sans-serif;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1350px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}


@import "mixins";

.custom-checkbox {
  @include custom-checkbox;

  &__content {
    padding-left: 35px;
    padding-right: 30px;
    font: var(--font-menu);
    color: var(--color-shiraz);

    &::before {
      top: -5px;
      border-color: var(--color-silver);
      border-radius: 50%;
      width: 30px;
      height: 30px;
    }

    &::after {
      top: -5px;
      border-radius: 50%;
      width: 30px;
      height: 30px;
      border: 5px solid var(--color-shiraz);
      background: none;
    }
  }
}

@media (max-width: 545px) {
  .custom-checkbox__content {
    &::before {
      width: 22px;
      height: 22px;
    }

    &::after {
      width: 22px;
      height: 22px;
    }
  }
}
