:root {
  --color-black: #000;
  --color-white: #fff;
  --color-shiraz: #aa0630;
  --color-shiraz-gradient: linear-gradient(180deg, #D33A61 0%, #AA0630 100%);
  --color-silver: #c4c4c4;
  --color-athensGray: #f1f0f5;
  --color-footer: #e9e8ee;

  //fonts
  --font-family-title: "Lifehack", sans-serif;
  --font-title: 400 60px / 130% var(--font-family-title);
  --font-title-medium: 400 48px / 100% var(--font-family-title);
  --font-secondtitle: 400 32px / 100% var(--font-family-title);
  --font-subtitle: 400 28px / 100% var(--font-family-title);
  --font-secondsubtitle: 400 24px / 100% var(--font-family-title);
  --font-title-footer: 400 20px / 100% var(--font-family-title);

  --font-family: "Rubik", sans-serif;
  --font-price: 700 32px / 100% var(--font-family);
  --font-text-medium: 500 32px / 100% var(--font-family);
  --font-num-medium: 500 24px / 100% var(--font-family);
  --font-text-tel: 700 24px / 100% var(--font-family);
  --font-text-light: 300 20px / 150% var(--font-family);
  --font-text-bold: 700 18px / 100% var(--font-family);
  --font-menu: 400 16px / 100% var(--font-family);
  --font-menu-text: 400 15px / 150% var(--font-family);
  --font-text: 400 14px / 150% var(--font-family);
  --font-text-date: 400 12px / 150% var(--font-family);
}
